import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import Slider from "react-slick"
import styles from './Footer.module.scss'
import config from '../../data/SiteConfig'


const settings = {
  autoplay: true,
  adaptiveHeight: true,
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesPerRow: 2,
  Rows: 2,	
  slidesToScroll: 1,
}


export default function EventCarousel() {
return (
<StaticQuery
query = {graphql`
 query yourquery {
 image2: file(relativePath: { eq: "gallery/DSC_0870.JPG" }) {
    childImageSharp {
     fixed (height: 190 width: 400){
      ...GatsbyImageSharpFixed
      }
      }
      }
 image3: file(relativePath: { eq: "gallery/DSC_0890.JPG" }) {
   childImageSharp {
     fixed (height: 219 width: 400){
       ...GatsbyImageSharpFixed
       } 
       }
       }
 image6: file(relativePath: { eq: "gallery/slide14.jpg" }) {
   childImageSharp {
     fixed (height: 300 width: 450){
       ...GatsbyImageSharpFixed
      }
       }
       }
 image7: file(relativePath: { eq: "gallery/slide4.jpg" }) {
   childImageSharp {
     fixed (height: 300 width: 450){
       ...GatsbyImageSharpFixed
       }
       }
       }
  image22: file(relativePath: { eq: "gallery/IMG_1834.jpg" }) {
              childImageSharp {
                fixed (height: 300 width: 450){
                  ...GatsbyImageSharpFixed
        }
        }
        }              
       }
      `}
render={data => (
  <div height='200px'>
  <Slider {...settings}>
  <Img fixed={data.image2.childImageSharp.fixed} />
  <Img fixed={data.image3.childImageSharp.fixed} />
  <Img fixed={data.image6.childImageSharp.fixed} />
  <Img fixed={data.image7.childImageSharp.fixed} />
  <Img fixed={data.image22.childImageSharp.fixed} />

   </Slider>
   </div>
)}
/>
)
}
